import uuid from "uuid";

//-- Expenses Action Generators
export const addExpense = (
  { description = "", note = "", amount = 0, createdAt = 0 } = {}
) => ({
  type: "ADD_EXPENSE",
  expense: {
    id: uuid(),
    description,
    note,
    amount,
    createdAt
  }
});

export const removeExpense = ({ id = "" } = {}) => ({
  type: "REMOVE_EXPENSE",
  id
});

export const editExpense = (id, payload) => ({
  type: "EDIT_EXPENSE",
  id,
  payload
});



